ndicator-image{max-width:100%;height:auto;border-radius:8px;box-shadow:0 4px 15px rgba(0,0,0,.2);border:1px solid rgba(244,244,244,.05);transition:transform .3s ease}.free-indicators .indicator-preview .indicator-image:hover{transform:scale(1.02)}.free-indicators .indicator-details{display:flex;flex-direction:column;padding:2rem;flex:1;color:#f4f4f4;text-align:left}.free-indicators .indicator-details h2{font-size:30px;color:#61ffbd;margin-bottom:1.2rem;font-weight:700}@media(min-width: 48em){.free-indicators .indicator-details h2{font-size:26px}}.free-indicators .indicator-details .description{font-size:16px;color:#d2d2d7;line-height:1.7;margin-bottom:1.8rem}@media(min-width: 48em){.free-indicators .indicator-details .description{font-size:18px}}.free-indicators .indicator-details .features{margin-bottom:2.5rem}.free-indicators .indicator-details .features h3{font-size:18px;color:#61ffbd;margin-bottom:1.2rem;font-weight:600}@media(min-width: 48em){.free-indicators .indicator-details .features h3{font-size:20px}}.free-indicators .indicator-details .features ul{list-style:none;padding-left:0;margin:0}.free-indicators .indicator-details .features ul li{position:relative;padding-left:2rem;margin-bottom:.8rem;line-height:1.6;color:#f4f4f4;font-size:16px}@media(min-width: 48em){.free-indicators .indicator-details .features ul li{font-size:18px}}.free-indicators .indicator-details .features ul li::before{content:"";color:#61ffbd;position:absolute;left:0;font-weight:bold;font-size:1.2em}.free-indicators .indicator-details .download-button{display:inline-flex;align-items:center;justify-content:center;background:#61ffbd;color:#04001c;padding:.8rem 2rem;border-radius:30px;text-decoration:none;font-weight:700;text-align:center;transition:all .3s ease;border:none;cursor:pointer;font-size:16px;text-transform:uppercase;letter-spacing:.5px;width:100%;max-width:300px;margin:0 auto}@media(min-width: 48em){.free-indicators .indicator-details .download-button{width:auto;padding:1rem 3r